/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* RESET COMPLETE ********************************* */
*, *:before, *:after {
  box-sizing: border-box;
}

#tree-background {
  position: fixed;
  z-index: -99;
  opacity: 0.95;
  width: 100vw;
  height: 100vh;
  background-color: black;
  background-image: url('../public/assets/Xmas\ tree.jpeg');
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  
}

h2, h3, h4, h5, p {
  margin: 5px 25px 10px;
  background-color: rgba(0,0,0,0.6);
  text-align: justify;
  color: lightgray;
}

h3, h4, h5 {
  margin-top: 5px;
}

ol, ul {
  margin: 0px 25px;
  /* background-color: rgba(0,0,0,0.7); */
  padding: 10px 30px;
}

ul {
  list-style: disc;
}

li {
  color: whitesmoke;
  background-color: rgba(0,0,0,0);
  padding: 10px 5px 10px 25px;
}

.sub-list {
  list-style-type: lower-alpha;
}

img {
  max-height: 95%;
  max-width: 100vw;
  text-align: center;
}

.img-holder {
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 450px;
  max-width: 100vw;
}

div img {
  max-height: 85%;
  margin: 0 auto;
}

h2 {
  font-size: 32px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  background-color: darkred;
  padding: 8px;
  border-radius: 20px;
  text-align: center;
  margin: 20px 5px;
  line-height: 50px;
}

h3 {
  font-size: 24px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  background-color: darkgreen;
  border-radius: 5px;
  padding: 10px;
  padding-left: 25px;
}

h4 {
  background-color: navy;
  border-radius: 10px;
  padding: 5px 5px 5px 5px;
}

.special-note {
  border-radius: 25px;
  border: dotted 5px yellow;
}
p, h4, li {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  line-height: 24px;
  padding: 5px 5px 5px 15px;
}

div p {
  margin: 5 auto;
}

a {
  color: lightblue;
  text-decoration: underline;
}

a:hover {
  color: purple;
  text-decoration: underline;
}

ol {
  list-style-type: decimal;
}

strong, b {
  font-weight: bold;
}

em, i {
  font-style: italic;
}

span {
  text-decoration: underline;
}

.video-holder {
  width: fit-content;
  margin: 0 auto;
}
.leisure {
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  height: 300px;
  vertical-align: baseline;
}
.leisure img {
  max-height: 98%;
}
video {
  display: flex;
  margin: 0 auto;
  outline: none;
}
#viewfromHPRC, #HPRC-lobby, #footbridge, #HKD10bill {
  display: flex;
  width: 50%;
  margin: 0 auto;
}
#quartets {
  display: flex;
  flex-direction: row;
  margin: 5 auto;
  height: 280px;
  width: 95%;
  justify-content: center;
  padding-top: 25px;
}
#outlet-plug, #blender {
  display: flex;
  max-width: 200px;
  margin: 0 auto;
}
.img-caption {
  margin: 0 auto;
  width: fit-content;
  font-size: medium;
}
#unionpaylogo {
  display: flex;
  width: 150px;
  margin: 0 auto;
}
.list-pic, .photo-moment {
  display: flex;
  max-width: 66%;
  margin: 0 auto;
  padding: 5px;
}
.hiking-pic {
  display: flex;
  max-width: 90%;
  margin: 0 auto;
}
#sunset-peak-view {
  display: flex;
  max-width: 90%;
  margin: 0 auto;
}
#fish-breakfast, #la-creperie {
  display: flex;
  max-height: 400px;
  margin: 0 auto;
}
iframe {
  display: flex;
  margin: 15px auto;
}

@media (min-width: 640px) and (min-height: 640px) {
  section {
    max-width: 70%;
    margin: 0 auto;
  }
}